import { defineStore } from 'pinia'
import { retrieveLaunchParams, retrieveRawInitData } from '@telegram-apps/sdk'

export const useAppStore = defineStore('app', {
  state: () => ({
    tgInitData: null,
    tgInitDataRow: null,
    apiBaseUrl: 'https://molnyads.tech', // <-- —Å—é–¥–∞ —Å–≤–æ–π URL
  }),
  actions: {
    async initTelegram() {
      // –µ—Å–ª–∏ –µ—â—ë –Ω–µ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω–æ
      if (!this.tgInitData) {
        // Optional: –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞—Ç—å —Å–∞–º WebApp, –µ—Å–ª–∏ –Ω—É–∂–µ–Ω
        const tg = window.Telegram?.WebApp
        if (tg) {
          tg.ready()
          tg.expand()
        }

        try {
          // retrieveLaunchParams —Ä–∞—Å–ø–∞—Ä—Å–∏—Ç —Å—Ç—Ä–æ–∫—É launchParams –≤ –æ–±—ä–µ–∫—Ç
          const params = await retrieveLaunchParams()
          const rawParams = await retrieveRawInitData()
          this.tgInitData = params;
          this.tgInitDataRow = rawParams;
          console.log('Telegram launch params:', this.tgInitData)
          console.log('Telegram launch params raw:', this.tgInitDataRow)
        } catch (e) {
          console.error('Cannot retrieve Telegram launch params', e)
        }
      }
    }
  }
})